.App{
    padding: 0;
    font-size: 0;
}

.quickScan{
    display: none;
}

#preview{
    width: 100%;
}

video{
    object-fit: cover;
    margin: 0;
    padding: 0;
    z-index: -1;
}

.scanResults{
    background: rgba(255,255,255,0.6);
    font-size: initial;
    position: absolute;
    left: 50%;
    bottom: 3%;
    transform: translate(-50%, 0);
    border-radius: 2em;
}

.scanResults h4{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.scanResults > div{
    background: rgba(255,255,255,.6);
    border-radius: 2em;
}

.dark-scanResults{
    color: white;
    background: rgba(127.5,127.5,127.5,0.6);
}

.dark-scanResults > div{
    background: rgba(127.5,127.5,127.5,.6);
}